const inspectScroller = (scroller: HTMLElement | Window): { rect: Record<string, number>, scrollTop: number } | null => {
  if (!scroller) return null;

  let rect;
  let scrollTop;
  if (scroller === window) {
    rect = {
      width: window.innerWidth || document.documentElement.clientWidth,
      height: window.innerHeight || document.documentElement.clientHeight,
    };
    scrollTop = scroller.scrollY;
  } else {
    const element: HTMLElement = scroller as HTMLElement;
    const { width, height } = element.getBoundingClientRect();
    rect = { width, height };
    scrollTop = element.scrollTop;
  }

  return {
    rect,
    scrollTop,
  };
}

export default inspectScroller;
